<template>
  <div id="app">
    <router-view/>
    <div id="quick-macro" v-show="show" tabIndex="1">
      <div class="header">
        <div class="clearfix headerTitle">
          <div class="title">按键精灵</div>
          <div class="cancel" @click="show = false"><i class="el-icon-close"></i></div>
        </div>
        <div class="inputCont">
            <el-input
              size="mini"
              v-model="keyword"
              id="keyword-input"
            >
            </el-input>
        </div>
      </div>
      <div class="list-wrapper" id="keyword-lists" ref='divlist' tabindex="0">
        <div class="list"  @click="activeCode = key[0]" @dblclick="goFunc(key)" :class="{ active: key[0] === activeCode }" v-for="key in keyLists" :key="key.code">
          <div class="code">{{ key[0] }}</div>
          <div class="name">{{ key[1] }}</div>
          <div class="func">{{ key[2] }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  provide() {
    return {
      keyword: this,
      keyLists: this,
      dataIndex: this,
      show: this
    }
  },
  created() {
    this.show = false
    console.log('在 appvue 41 行打印' + this.show)
  },
  mounted() {
    /* 全局按钮事件 */
    window.addEventListener('keydown', e => {
      console.log('获取当前页面' + this.$route.path + this.STOCK.isPushKline + this.STOCK.isKlineInput)
      if (this.$route.path === '/kline') {
        if (this.STOCK.isKlineInput === false) {
          if (e.keyCode === 13 && this.STOCK.isPushKline === false) {
            this.$router.push({ name: 'TimeShare', params: { code: this.STOCK.code } })
            return
          }
          if (e.key === 'Escape') {
            this.STOCK.isPushKline = false
            this.$router.replace({ name: 'ShareList' })
            return
          }
        }
        this.STOCK.isPushKline = false
        this.STOCK.isKlineInput = false
      } else if (this.$route.path === '/time_share') {
        if (e.keyCode === 13 && this.show === false) {
          // this.$router.go(-1)
          this.$router.replace({ name: 'kline' })
          return
        }
        if (e.key === 'Escape') {
          this.STOCK.isPushKline = false
          this.$router.replace({ name: 'ShareList' })
          return
        }
      } else if (this.$route.path === '/') {
        console.log('进入到了/' + this.STOCK.isPushKline)
        if (e.keyCode === 13 && this.STOCK.isPushKline === true) {
          this.$router.replace({ name: 'kline', params: { code: this.STOCK.code } })
          this.STOCK.isPushKline = false
        }
      }
      const isValid = e.keyCode >= 48 && e.keyCode <= 90
      if (this.show && isValid) {
        const conversion = this.conversionKey[e.keyCode]
        this.keyLists = []
        const text = this.keyword + conversion
        console.log(e.keyword + 'test')

        if (text.length) {
          var url = this.baseUrl + '/stock_info/find/par=' + text + '/'
          this.keyLists = []
          this.$http.get(url).then((response) => {
            this.keyLists = JSON.parse(JSON.stringify(response.body.data))
            this.keyListsData = JSON.parse(JSON.stringify(response.body.data))
            this.dataIndex = 0
            console.log('连接成功')
          }, (response) => {
            console.log('连接出错')
          }).catch(function(response) {
            console.log('连接catch')
          })
        }
      }

      if (e.keyCode === 13) {
        if (this.show) {
        }
        this.keyLists = []
        this.keyword = ''
        // console.log(this.$refs)
        // const text = this.keyword
        // if (text.length) {
        //   var url = this.baseUrl + '/stock_info/find/par=' + text + '/'
        //   this.$http.get(url).then((response) => {
        //     this.keyLists = JSON.parse(JSON.stringify(response.body.data))
        //     this.keyListsData = JSON.parse(JSON.stringify(response.body.data))
        //     this.$refs.divlist.focus()
        //     console.log('连接成功')
        //   }, (response) => {
        //     console.log('连接出错')
        //   }).catch(function(response) {
        //     console.log('连接catch')
        //   })
        // }
      } else {
        /* 字母和数字 */
        const isValid = e.keyCode >= 48 && e.keyCode <= 90
        if (isValid) {
          this.keyLists = []

          if (this.show) {
            return
          }
          this.show = isValid
          console.log('在 appvue 100 行打印' + this.show)
          setTimeout(() => {
            document.getElementById('keyword-input').focus()
            this.isHttp = true
          }, 500)
        }
        /* ESC */
        // if (e.key === 'Escape') {
        //   this.$router.go(-1)
        // }
      }
    })
    /* 点击其他地方隐藏按键精灵 */
    document.addEventListener('click', e => {
      this.show = false
      console.log('在 appvue 115 行打印' + this.show)
      this.isHttp = false
      this.keyword = ''
    })

    document.getElementById('quick-macro').addEventListener('click', e => {
      // console.log(e)
      e.stopPropagation()
      this.show = false
      console.log('在 appvue 123 行打印' + this.show)
      this.keyword = ''
      // this.$router.push({
      //   path: '/kline'
      // })
    })
    /* 按键精灵上下键 */
    document.getElementById('quick-macro').addEventListener('keydown', e => {
      if (e.keyCode === 38) {
        this.isHttp = false
        if (this.dataIndex > 1) {
          this.dataIndex--
          console.log(this.dataIndex)
          this.$refs.divlist.focus()
          this.keyLists.unshift(this.keyListsData[this.dataIndex - 1])
          this.activeCode = this.keyLists[0][0]
        }
      }
      /* 下 */
      if (e.keyCode === 40) {
        this.isHttp = false
        if (this.dataIndex === 0) {
        } else {
          this.keyLists.splice(0, 1)
        }
        this.$refs.divlist.focus()
        console.log(this.dataIndex)
        this.activeCode = this.keyLists[0][0]
        this.dataIndex++
      }

      /* enter */
      if (e.keyCode === 13) {
      }
    })
  },
  data() {
    return {
      keyword: '',
      baseUrl: 'http://172.18.3.50:8080',
      activeCode: 1,
      dataIndex: 0,
      show: false,
      isHttp: true,
      conversionKey: {
        48: '0',
        49: '1',
        50: '2',
        51: '3',
        52: '4',
        53: '5',
        54: '6',
        55: '7',
        56: '8',
        57: '9',
        65: 'a',
        66: 'b',
        67: 'c',
        68: 'd',
        69: 'e',
        70: 'f',
        71: 'g',
        72: 'h',
        73: 'i',
        74: 'j',
        75: 'k',
        76: 'l',
        77: 'm',
        78: 'n',
        79: 'o',
        80: 'p',
        81: 'q',
        82: 'r',
        83: 's',
        84: 't',
        85: 'u',
        86: 'v',
        87: 'w',
        88: 'x',
        89: 'y',
        90: 'z'
      },
      keyLists: [
      ],
      keyListsData: [
      ]
    }
  },
  methods: {
    goFunc(key) {
      console.log(key)
    }
  },
  watch: {
    keyword(value) {
      if (value === '') {
        // this.show = false
        console.log('在 appvue 221 行打印' + this.show)
      }
    }
  }
}
</script>
